.spinLogo {
    -webkit-animation: autoSpinLogo infinite 20s linear;
            animation: autoSpinLogo infinite 20s linear;
    height: 40vmin;
    font-size: 8em;
    color: rgb(119, 174, 255, 0.5);
    /* pointer-events: none; */
    transition: 0.3s all ease-out;
    padding: 5px 5px 5px 5px;
}
.spinLogo:hover {
    font-size: 10em;
    color: rgb(163, 199, 251, 0.8);
  }

@-webkit-keyframes autoSpinLogo {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes autoSpinLogo {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

