/*boxflex system*/
.box {
    display: -webkit-inline-flex;
    display: inline-flex;
    padding: 5vw 5h;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 100%;
    height: 100%;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
}

.boxstyle {
    -webkit-flex: 1 1 1;
            flex: 1 1 1;
    width: 300px;
    height: 200px;
    margin: 30px 30px 30px 30px;
    text-align: center;
    line-height: 150px;
    border-radius: 10px 10px 10px 10px;
    border-color: blue;
    border-width: 1px;
    border-style: none;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    transition: all 500ms;
}

.boxstyle:hover {
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.5);
    transition: scale(1.1);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.boxstyle img {
    overflow: hidden;
}
/*建立元件的樣式表*/
/*https://developer.mozilla.org/zh-TW/docs/Web/CSS/transition*/
.textGroup {
    width: 100%;
    height: 2.5em;
    border-width: 2px;
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
    background-color: #ffffff;
    transition: all 1s ease-out;
}

.msgGroup {
    width: 100%;
    height: 300px;
    border-width: 2px;
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
    background-color: #ffffff;
    transition: all 1s ease-out;
}

.btnGroup {
    width: 200px;
    height: 60px;
    border-width: 2px;
    border-radius: 5px;
    padding: 10px;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 20px;
    background-color: #808080;
    transition: all 1s ease-out;
}
/*uparrow*/
.uparrow {
    color: rgb(255, 255, 255);
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.50);
    border-radius: 50%;
    position: fixed;
    font-size: 3em;
    bottom: 20px;
    right: 20px;
    z-index: 2000;
    transition: all 200ms;
}

.uparrow.arrowmove {
    right: -200px;
}

.uparrow:hover {
    color: rgb(200, 230, 235);
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.5);
    transition: scale(1.5);
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.uparrow:active {
    color: rgb(0, 80, 140);
}
